




















import UploadNexus from '@/components/global/UploadNexus.vue';
import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
  components: {
    UploadNexus
  }
})
export default class UploadButton extends Vue {
  showModal = false;
  /**
   * Invoked when file(s) are loaded.
   */
  close_modal() {
    this.showModal = false;
  }

  show_modal() {
    this.showModal = true;
  }
}
